.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
nav{
  background-color: aliceblue;
  height: 50px;
  color: blueviolet;
  font-weight: 600;
  font-size: 30px;
}
.productos{
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 90%;
  margin: auto;
  margin-top: 40px;

}
body{
  background-color:#282c34 ;
}
.contenedor_producto{
  width: 300px;
  background-color: rgb(29, 28, 28);
  margin: auto;
  color: white;
  margin-top: 20px;
}

.contenedor_producto img{
  width: 300px;
  border-bottom: 1px solid black;
}
.contenedor_producto h2{
  font-size: 20px;
  font-weight: 300;
 
}
.contenedor_producto button{
  margin-bottom: 10px;
  border: none;
  background-color: blueviolet;
  color: white;
  font-size: 13px;
  font-weight: 100;
  width: 80px;
  height: 30px;
  transition: .6s;
 
}

.contenedor_producto button:hover{
  background-color: rgb(88, 27, 145);
}
.contenedor_producto h3{
  font-size: 20px;
  font-weight: 300;
 
}

.cart{
  display: flex;
  flex-direction:column;
  background-color:#20242a ;

  color: wheat;
 
}
.contenedor_carrito{
  width: 300px;
  background-color: rgb(29, 28, 28);
  margin: auto;
  color: white;
  margin-top: 20px;
  margin-bottom: 10px;
}
.contenedor_carrito img{
  width: 300px;
  border-bottom: 1px solid black;
}
.contenedor_carrito h2{
  font-size: 20px;
  font-weight: 300;
 
}
.contenedor_carrito h3{
  font-size: 20px;
  font-weight: 300;
 
}
.contenedor_carrito button{
  margin-bottom: 10px;
  border: none;
  background-color: blueviolet;
  color: white;
  font-size: 13px;
  font-weight: 100;
  width: 80px;
  height: 30px;
  transition: .6s;
 
}

.contenedor_carrito button:hover{
  background-color: rgb(88, 27, 145);
}

.title_cart{
  height: 60px;
  text-align: center;
  margin: auto;
  font-size: 30px;
  font-weight: 100;
  background-color: black;
  color: white;
  margin-top: 30px;
}
.one{
  margin-right: 10px;
}
.all{
  margin-left: 10px;
}
.total{
  font-weight: 100;
  font-size: 20px;
  color: white;
}
.send{
  margin-bottom: 10px;
  border: none;
  background-color: rgb(59, 156, 59);
  color: white;
  font-size: 13px;
  font-weight: 100;
  width: 80px;
  height: 30px;
  transition: .6s;
 
}
.send:hover{
  background-color: rgb(44, 113, 44);
}

@media (min-width:720px){
  .cart{
    flex-direction:row
  }
  .productos{
    flex-direction: row; 
  }
  .contenedor_producto{
    width: 300px;
    height: 400px;
    background-color: rgb(29, 28, 28);
    margin: auto;
    color: white;
    margin-top: 20px;
  }
}